import React from "react";
import Section from "./Section";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";

function _toFixedNum(str) {
    if (typeof str == 'undefined') { return '' }
    return parseFloat(str).toFixed(4)
}
function _toMegabucks(str) {
    if (typeof str == 'undefined') { return '' }
    let num = parseFloat(str)
    let megas = num/1000000
    return '$' + (megas < 1 ? megas.toFixed(4) : megas.toFixed(1)) + 'M'
}

// pool and totalWeight not strictly needed
function spotPrice(tokenIn, tokenOut, pool) {
  let Bo = tokenOut.balance
  let Bi = tokenIn.balance
  let Wi = tokenIn.denormWeight / pool.totalWeight
  let Wo = tokenOut.denormWeight / pool.totalWeight
  return (Bo / Wo) / (Bi / Wi)
}

function PoolStats(props) {
  let spot = props.pool.tokens.length == 0 ? 0 : spotPrice(props.pool.tokens[0], props.pool.tokens[1], props.pool)

  const items = props.pool.tokens.length == 0 ? [] : [
    {
      title: "Spot Price",
      stat: _toFixedNum(spot)
    },
    {
      title: "Inverse Spot Price",
      stat: _toFixedNum(1/spot)
    },
    {
      title: props.pool.tokens[0].symbol,
      stat: _toFixedNum(props.pool.tokens[0].balance)
    },
    {
      title: props.pool.tokens[1].symbol,
      stat: _toFixedNum(props.pool.tokens[1].balance)
    },
    {
      title: '1 BPT in ' + props.pool.tokens[0].symbol,
      stat: _toFixedNum(props.pool.tokens[0].balance / props.pool.totalShares)
    },
    {
      title: '1 BPT in ' + props.pool.tokens[1].symbol,
      stat: _toFixedNum(props.pool.tokens[1].balance / props.pool.totalShares)
    },
    {
      title: "Market Cap",
      stat: _toMegabucks(props.pool.liquidity)
    },
    {
      title: "Total Shares",
      stat: _toFixedNum(props.pool.totalShares)
    },
    {
      title: "Swap Fee",
      stat: _toFixedNum(100*props.pool.swapFee) + '%'
    },
  ];

  return (
    <Section
      bgColor={props.bgColor}
      size={props.size}
      bgImage={props.bgImage}
      bgImageOpacity={props.bgImageOpacity}
    >
      <Container>
        <Grid container={true} justify="center" spacing={4}>
          {items.map((item, index) => (
            <Grid item={true} xs={12} sm={3} key={index}>
              <Box textAlign="center">
                <Typography variant="overline">{item.title}</Typography>
                <Typography variant="h4">{item.stat}</Typography>
              </Box>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Section>
  );
}

export default PoolStats;
